
.calendar-input-container {
  display: inline-block;
  position: relative;
  padding: 1rem;
  text-align: center;
  font-family: SimHei;
}
.calendar-input-container .fade-enter-active,
  .calendar-input-container .fade-leave-active {
    transition: opacity .5s;
}
.calendar-input-container .fade-enter,
  .calendar-input-container .fade-leave-active {
    opacity: 0;
}
.calendar-input-container .calendar-input {
    box-sizing: border-box;
    width: 20rem;
    height: 3rem;
    padding-left: .5rem;
    padding-right: .5rem;
    border: .1rem solid #ccc;
    border-radius: 4%;
    background-color: #fff;
    cursor: pointer;
    outline: none;
    transition: all .6s;
}
.calendar-input-container #calendar {
    position: absolute;
    width: 20rem;
    height: 24rem;
    box-sizing: border-box;
    border: .2rem solid #eee;
    border-radius: 2%;
    background-color: #fff;
}
.calendar-input-container #calendar div, .calendar-input-container #calendar span {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.calendar-input-container #calendar div {
      height: 12.5%;
      width: 100%;
}
.calendar-input-container #calendar span {
      height: 100%;
      width: 14.2857%;
}
.calendar-input-container #calendar #calendar-header {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.calendar-input-container #calendar #calendar-header span {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
.calendar-input-container #calendar #calendar-header #date-box {
        -webkit-box-flex: 2;
            -ms-flex-positive: 2;
                flex-grow: 2;
}
.calendar-input-container #calendar .week {
      box-sizing: border-box;
      padding: .5rem;
      font-size: 1.6rem;
}
.calendar-input-container #calendar .week span {
        height: 100%;
}
.calendar-input-container #calendar .days {
      box-sizing: border-box;
      height: 75%;
      padding: .5rem;
      font-size: 1.4rem;
}
.calendar-input-container #calendar .days span {
        height: 16.666667%;
        cursor: pointer;
        transition: all .5s;
}
.calendar-input-container #calendar .days .unselect {
        cursor: default;
}
.orange-theme .focus {
  border-color: #f0a54f;
  box-shadow: 0 0 0.5rem #f0a54f;
}
.orange-theme .arrow {
  cursor: pointer;
  color: #ee7419;
  transition: color .5s;
}
.orange-theme .arrow:hover {
    color: #fcf0e5;
}
.orange-theme .weekend {
  color: #ee7419;
}
.orange-theme .days span:hover {
  border-radius: .2rem;
  background-color: #f0a54f;
  color: #fff;
}
.orange-theme .days .unselect,
.orange-theme .days .unselect:hover {
  border-radius: 0;
  background-color: #fff;
  color: #ccc;
}
.orange-theme .days .select,
.orange-theme .days .select:hover {
  border-radius: .2rem;
  background-color: #ee7419;
  color: #fff;
}



